import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';



/**
 * Generated class for the MisReservasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mis-reservas',
  templateUrl: 'mis-reservas.html',
})
export class MisReservasPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController,public events: Events, public apiProvider: ApiProvider) {
   this.reservas=[];


  }


  //ionViewDidLoad() {
  ionViewDidEnter() {
    this.section="one";
    console.log('ionViedLoad MisReservasPage');

        this.apiProvider.verificarLogin()
    .then(data => { 

      console.log(data);

      if(data){

      this.dataUser = data;
      this.getReservas();
      //this.menuActivo = true;
      }
      else{
      console.log('error');
      // this.menuActivo = false;
      } 
      
      
     });


  }

    filterFav(user: any) {
  console.log(user);
    return user.estado == 1 || user.estado == 2 || user.estado == 5
  }

    filterFav2(user: any) {
  console.log(user);
    return user.estado == 3 || user.estado == 4
  }


      goDetalle(idCita) {


     //this.navCtrl.push('DetalleReservaPage');

       this.navCtrl.push('DetalleReservaPage', {idCita:idCita});


  
      
  }

  getReservas(){

        this.apiProvider.reservasUser({idCliente:this.dataUser.idCliente})
      .then(data => {
       console.log(data);
              if(data){
        this.reservas = data || [];
       }
       else{console.log('Ha ocurrido un error');}

       
      });

  }




  

}



